/* font */
@font-face {
  font-family: "Happiness-Sans-Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/Happiness-Sans-Bold.woff2")
    format("woff2");
  font-weight: 700;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #f90791;
}
img {
  vertical-align: top;
}
body {
  /* 변수로 만들어서 쓰는 이미지는 src 안에
  그냥 이렇게 이미지 넣을땐 public안에 넣어두자. */
  background: url(images/a.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  color: white;
}
.logo {
  font-weight: 600;
  font-size: 1.5em;
  padding-top: 10px;
  color: white;
}
.innerBox {
  max-width: 1200px;
  margin: 0 auto;
}
header {
  border-bottom: 1px solid #333;
}
header > .innerBox {
  display: flex;
  justify-content: space-between;
}
#nav {
  display: flex;
  /* justify-content: ; */
  align-items: center;
  gap: 50px;
  padding: 20px 0;
}
#nav > li > a {
  color: white;
}

.container {
  padding: 200px 0;
}

/* Home */
.titleBox {
  text-align: center;
}
.titleBox > p {
  font-family: "Happiness-Sans-Bold";
  display: block;
  font-size: 3.5em;
}
.titleBox span {
  color: #f90791;
}
.maskText {
  padding-top: 20px;
}
.maskText > .mask {
  font-size: 2em;
  font-weight: bold;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#edb6dc+0,c59ed8+46,a28fc4+100 */
  background: rgb(237, 182, 220); /* Old browsers */
  background: -moz-linear-gradient(
    left,
    rgba(237, 182, 220, 1) 0%,
    rgba(197, 158, 216, 1) 46%,
    rgba(162, 143, 196, 1) 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    rgba(237, 182, 220, 1) 0%,
    rgba(197, 158, 216, 1) 46%,
    rgba(162, 143, 196, 1) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    rgba(237, 182, 220, 1) 0%,
    rgba(197, 158, 216, 1) 46%,
    rgba(162, 143, 196, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#edb6dc', endColorstr='#a28fc4',GradientType=1 ); /* IE6-9 */

  /* 글자안에 그라데이션 넣기 */
  background-clip: text;
  color: rgba(0, 0, 0, 0);
}
