.aboutWrap a{
    color: blue;
}

.titleBox h2{
    font-size: 2.5em;
    padding: 20px 0;
}
.aboutWrap{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}
.aboutWrap .bgImg{
    flex: 1
}
.aboutWrap .bgImg img{
    width: 100%;
}
.aboutWrap .box{
    background: white;
    color: #444;
    display: flex;
    margin-bottom: 20px;
    border-radius: 10px;
    padding: 40px 60px;
    column-gap: 20px;
    align-items: center;
    transition: 0.2s;
}
.aboutWrap .box:hover{
    background: #F90791;
    color: white;
}
.aboutWrap .box .imgBox{
    margin-right: 50px;
}
.aboutWrap .box img{
    width: 70px;
}
.textBox strong{
    font-size: 1.5em;
    color: #3E2A6F
}
.aboutWrap .box:hover strong{
    color: white;
}