.todoItem{
    display: flex;
    cursor: pointer;
    border-bottom: 1px dashed #ddd;
    line-height: 2.5;
    padding: 5px;
}
.remove{
    color: rgb(118, 166, 174);
    opacity: 0;
    font-weight: bold;
    margin-right: 10px;    
}
.todoItem:hover .remove{
    opacity: 1;
}
.checked{
    color: #aaa;
    text-decoration: line-through;
}