.form{
    display: flex;
    margin-bottom: 30px;
}
.form input{
    line-height: 2.5;
    font-size: 12px;
    flex: 1;
    border: 1px solid lightgray;
    border-radius: 10px;
    text-indent: 20px;
}
.form input:focus{
    outline-color: lightgray;
}
.form button{
    background: rgb(118, 166, 174);
    margin-left: 10px;
    padding: 15px 10px;
    border: none;
    color: white;
    border-radius: 10px;
}